.burger-letter {
    font-family: 'Lobster', cursive;
    font-size: 800%;
    text-align: center;
    color: white;
}

.button-welcome {

    background: rgba(196, 196, 196, 0.7);
    border-radius: 2rem;

    font-family: lobster;
    font-style: normal;
    font-weight: normal;
    font-size: 500%;
    line-height: 87px;
    display: flex;
    align-items: center;

    position: absolute;
    width: 20%;
    height: 28%;
}

.btn-waitress {
    left: 12%;
    top: 30%;
}

.btn-kitchen {
    right: 12%;
    top: 30%;
}

.link {
    color: black;
}